@import '../../app/styles/vars';
.label {
  cursor: pointer;
  position: relative;
  display: block;
  width: 32px;
  height: 32px;
  @media (max-width: $landscape-phones-max) {
    width: 22px;
    height: 22px;
  }
  .radio {
    position: absolute;
    z-index: -1;
    opacity: 0;
  }
}
